<template>
    <div>
      <h2>订单</h2>
      <button v-on:click="piw">hh</button>
      <div>{{mm}}</div>
      <h1  @click="drawer = true">傻逼</h1>
<el-drawer
  title="我是标题"
  :visible.sync="drawer"
  :direction='direction'
  :before-close="handleClose">
  <button>按钮</button>
</el-drawer>
    </div>
  </template>
  
  <script>
  
  export default {
    mounted(){
      this.piw()
    },
    data(){
      return{
        count: 0,
        drawer: false,
        direction: 'ltr',
        mm:''
      }
    },
    methods:{
      load () {
        this.count += 2
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      piw(){
        var e = 6;
			  var t = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678";
			  var a = t.length;
			  var n = "";
			  for (var i = 0; i < e; i++){
          n += t.charAt(Math.floor(Math.random() * a));
        } 
        this.mm=n
        console.log(this.mm);
      },
      randomString(e) {  
			  var e = e || 32;
			  var t = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678";
			  var a = t.length;
			  var n = "";
			  for (var i = 0; i < e; i++){
          n += t.charAt(Math.floor(Math.random() * a));
        } 
			  return n
			}
    }
    
  }
  </script>
  
